// app/components/ui/loading.tsx
'use client';

import React, { useEffect, useState } from 'react';

type LoadingProps = {
  size?: 'small' | 'medium' | 'large';
  message?: string;
};

type ErrorDisplayProps = {
  message: string;
  details?: string;
  size?: 'small' | 'medium' | 'large';
};

type EmptyDataProps = {
  title: string;
  message?: string;
  size?: 'small' | 'medium' | 'large';
};

export default function Loading({ size = 'medium', message }: LoadingProps) {
  const [dots, setDots] = useState('');
  
  // เอฟเฟ็กต์จุดเคลื่อนไหว
  useEffect(() => {
    const interval = setInterval(() => {
      setDots(prevDots => (prevDots.length >= 3 ? '' : prevDots + '.'));
    }, 500);
    
    return () => clearInterval(interval);
  }, []);
  
  const sizeClass = {
    small: 'w-8 h-8',
    medium: 'w-16 h-16',
    large: 'w-24 h-24'
  };

  return (
    <div className="flex flex-col items-center justify-center p-8 text-center">
      <div className="relative">
        <div className={`${sizeClass[size]} rounded-full bg-blue-100 flex items-center justify-center`}>
          <div className={`animate-spin ${sizeClass[size]} rounded-full border-4 border-blue-600 border-opacity-30 border-t-blue-600`}></div>
        </div>
        <div className="absolute inset-0 flex items-center justify-center">
          <svg className="w-1/2 h-1/2 text-blue-600" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 10V3L4 14h7v7l9-11h-7z" />
          </svg>
        </div>
      </div>
      
      {message && (
        <div className="mt-6 bg-white px-6 py-3 rounded-full shadow-md text-blue-800 font-medium animate-pulse">
          {message}<span className="inline-block w-6 text-left">{dots}</span>
        </div>
      )}
    </div>
  );
}

export function ErrorDisplay({ message, details, size = 'medium' }: ErrorDisplayProps) {
  const sizeClass = {
    small: 'text-xl',
    medium: 'text-2xl',
    large: 'text-3xl'
  };

  return (
    <div className="text-center p-8 bg-white rounded-lg shadow-lg border-l-4 border-red-500">
      <div className="inline-flex items-center justify-center w-20 h-20 rounded-full bg-red-100 mb-4">
        <svg className="w-10 h-10 text-red-500" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z"></path>
        </svg>
      </div>
      <h2 className={`${sizeClass[size]} font-bold text-gray-800 mb-3`}>{message}</h2>
      {details && (
        <div className="mt-4 p-4 bg-red-50 rounded-lg text-left">
          <p className="text-red-700">{details}</p>
        </div>
      )}
      <div className="mt-6">
        <button onClick={() => window.location.reload()} className="px-5 py-2 bg-red-500 text-white rounded-lg hover:bg-red-600 transition-colors shadow-md">
          ลองใหม่อีกครั้ง
        </button>
      </div>
    </div>
  );
}

export function EmptyData({ title, message, size = 'medium' }: EmptyDataProps) {
  const sizeClass = {
    small: 'text-xl',
    medium: 'text-2xl',
    large: 'text-3xl'
  };

  return (
    <div className="text-center p-8 bg-white rounded-lg shadow-lg border-t-4 border-gray-200">
      <div className="inline-flex items-center justify-center w-20 h-20 rounded-full bg-gray-100 mb-4">
        <svg className="w-10 h-10 text-gray-500" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M20 13V6a2 2 0 00-2-2H6a2 2 0 00-2 2v7m16 0v5a2 2 0 01-2 2H6a2 2 0 01-2-2v-5m16 0h-2.586a1 1 0 00-.707.293l-2.414 2.414a1 1 0 01-.707.293h-3.172a1 1 0 01-.707-.293l-2.414-2.414A1 1 0 006.586 13H4"></path>
        </svg>
      </div>
      <h2 className={`${sizeClass[size]} font-bold text-gray-700 mb-3`}>{title}</h2>
      {message && <p className="text-gray-500 max-w-md mx-auto">{message}</p>}
      <div className="mt-6 border-t border-gray-100 pt-6">
        <div className="w-16 h-1 bg-gray-200 rounded-full mx-auto"></div>
      </div>
    </div>
  );
}